let seried = () => { // 
  let arr = [];
  for (let i = 0; i < 10; i++) {
    arr.push({ // 
      name: '',
      type: 'bar',
      data: [10, 10, 10],
      barWidth: 16,
      stack: 'st',
      z: 3,
      itemStyle: {
        color: 'transparent',
        borderWidth: 4,
        barBorderRadius: [1, 1, 1, 1],
        borderColor: '#191F3A'
      }
    });
  }
  return arr;
};
