<template>
    <div class="farther" :class="parentClass" v-loading="loading">
        <article>{{title}}</article>
        <div style="height:calc(100% - 15px);">
            <component :chartType="chartType" ref="chartTemplate" :is="'dc-chart-template-' + chartType" :theme="theme"
                       :id="specialId" :chartData="chartData"  v-if="isShowChart" style="height:100%;">
            </component>
            <div v-show="!isShowChart && chartMsg" class="child" v-text="chartMsg"></div>
        </div>
    </div>

</template>

<script>
import pie from '@/myPlugin/echarts-template/pie/pie.vue'; // 
import ring from '@/myPlugin/echarts-template/ring/ring.vue'; // 
import gauge from '@/myPlugin/echarts-template/gauge/gauge.vue'; // 
import line from '@/myPlugin/echarts-template/line/line.vue'; // 
import number from '@/myPlugin/echarts-template/number/number.vue'; // 
import column from '@/myPlugin/echarts-template/column/column.vue'; // 
import stack from '@/myPlugin/echarts-template/stack/stack.vue'; // 
import bars from '@/myPlugin/echarts-template/bars/bars.vue'; // 
import area from '@/myPlugin/echarts-template/area/area.vue'; // 
import target from '@/myPlugin/echarts-template/target/target.vue'; // 
import table from '@/myPlugin/echarts-template/table/table.vue'; // 
import map from '@/myPlugin/echarts-template/map/map.vue'; // 
export default {
  name: 'VueEchartsTcsl',
  data () {
    return {
      loading: true,
      isShowChart: false,
      chart: '',
      chartMsg: ''
    };
  },
  computed: {
    parentClass () {
      return `chart-parent-${this.theme}`;
    }
  },
  props: {
    chartType: {
      type: String,
      required: true
    },
    chartData: Object,
    title: {
      type: String,
      required: true,
      default: function () {
        return '';
      }
    },
    specialId: {
      type: String,
      required: true
    },
    theme: {
      type: String,
      default: function () {
        return 'blue';
      }
    }
  },
  mounted () {
    setTimeout(_ => {
      this.loaderStop();
    }, 1000);
  },
  methods: {
    loaderStop () {
      this.loading = false;
      this.isShowChart = true;
      // this.chartMsg = '';
    },
    loader () {
      this.loading = true;
    },
    resize () {
      try {
        this.$refs.chartTemplate.resize();
      } catch (e) {
        console.trace(`'dc-chart-template-${this.chartType}' component unMounted`);
        /* console.log('the chart unMounted');
        console.info('the chart unMounted');
        console.error('the chart unMounted');
        console.warn('the chart unMounted'); */
      }
    }
  },
  components: { // 
    'dc-chart-template-pie': pie, // 
    'dc-chart-template-ring': ring, // 
    'dc-chart-template-gauge': gauge, // 
    'dc-chart-template-line': line,// 
    'dc-chart-template-number': number, // 
    'dc-chart-template-column': column, // 
    'dc-chart-template-stack': stack, // 
    'dc-chart-template-bars': bars, // 
    'dc-chart-template-area': area, // 
    'dc-chart-template-target': target, // 
    'dc-chart-template-table': table, // 
    'dc-chart-template-map': map // 
  }
};
</script>
<style lang="scss">
    .farther {
        .el-loading-mask{
            background-color: transparent;
        }
    }
</style>
<style lang="scss" scoped>
    .farther {
        width: 100%;
        height: 100%;
    }
    @mixin chart-parent($font-color, $border-image){
        font-size:18px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color: $font-color;
        overflow: hidden; //
        border:15px solid transparent;
        border-image: url($border-image) 30 30 stretch;
        -moz-border-image:url($border-image) 30 30 stretch;/* Old Firefox */
        -webkit-border-image:url($border-image) 30 30 stretch;/* Safari and Chrome */
        -o-border-image:url($border-image) 30 30 stretch;
    }
    .chart-parent-purple{
        $font-color: rgba(226,202,255,1);
        $border-image: '~@/assets/static/chart/purple_border.png';
        background:rgba(139,45,255,0.07);
        @include chart-parent($font-color, $border-image);
    }
    .chart-parent-orange{
        $font-color: #FFB617;
        $border-image: '~@/assets/static/chart/orange_border.png';
        background:rgba(31,21,13,0.5);
        @include chart-parent($font-color, $border-image);
    }
    .chart-parent-blue{
        $font-color: #65B8FF;
        $border-color: rgba(63,95,252,1);
        @include chart-parent($font-color, '');
        > div{
            margin-top: 5px;
            padding-top: 8px;
            border-top: $border-color 1px solid;
            background:rgba(80,102,244, .1);
            background-clip: content-box;
        }
        &:before{
            $height: 3px;
            content:"..";
            color: transparent;
            background-color:$border-color;
            height: $height;
            width: $height * 2;
            margin-top: 26px - ($height / 2);
            position: absolute;
            z-index: 2;
        }
    }
</style>
